// @use 'settings/variables' as v;
// @use 'tools/mixins' as m;

.c-button {
    border: 2px solid #fff;
    border-radius: 15px;
    color: #fff;
    padding: 10px 15px;
    font-size: 15px;
    cursor: pointer;
    &:hover {
        background: #10209A40;
    }
    &--amarillo {
        // border: 2px solid map-get(v.$colores, color-fondo);
        border: 2px solid rgb(255, 193, 0);
        background: rgb(255,193,0);
        background: -moz-radial-gradient(circle, rgba(255,193,0,0.6474964985994398) 0%, rgba(255,193,0,1) 75%);
        background: -webkit-radial-gradient(circle, rgba(255,193,0,0.6474964985994398) 0%, rgba(255,193,0,1) 75%);
        background: radial-gradient(circle, rgba(255,193,0,0.6474964985994398) 0%, rgba(255,193,0,1) 75%);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffc100",endColorstr="#ffc100",GradientType=1);
        &:hover {
            background: rgb(255,193,0);
            background: -moz-radial-gradient(circle, rgba(255,193,0,1) 0%, rgba(255,193,0,0.6474964985994398) 40%);
            background: -webkit-radial-gradient(circle, rgba(255,193,0,1) 0%, rgba(255,193,0,0.6474964985994398) 40%);
            background: radial-gradient(circle, rgba(255,193,0,1) 0%, rgba(255,193,0,0.6474964985994398) 40%);
            filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffc100",endColorstr="#ffc100",GradientType=1);
        }
    }

}