.c-button {
  border: 2px solid #fff;
  border-radius: 15px;
  color: #fff;
  padding: 10px 15px;
  font-size: 15px;
  cursor: pointer;
}
.c-button:hover {
  background: rgba(16, 32, 154, 0.2509803922);
}
.c-button--amarillo {
  border: 2px solid rgb(255, 193, 0);
  background: rgb(255, 193, 0);
  background: -moz-radial-gradient(circle, rgba(255, 193, 0, 0.6474964986) 0%, rgb(255, 193, 0) 75%);
  background: -webkit-radial-gradient(circle, rgba(255, 193, 0, 0.6474964986) 0%, rgb(255, 193, 0) 75%);
  background: radial-gradient(circle, rgba(255, 193, 0, 0.6474964986) 0%, rgb(255, 193, 0) 75%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffc100",endColorstr="#ffc100",GradientType=1);
}
.c-button--amarillo:hover {
  background: rgb(255, 193, 0);
  background: -moz-radial-gradient(circle, rgb(255, 193, 0) 0%, rgba(255, 193, 0, 0.6474964986) 40%);
  background: -webkit-radial-gradient(circle, rgb(255, 193, 0) 0%, rgba(255, 193, 0, 0.6474964986) 40%);
  background: radial-gradient(circle, rgb(255, 193, 0) 0%, rgba(255, 193, 0, 0.6474964986) 40%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffc100",endColorstr="#ffc100",GradientType=1);
}